import { Icon } from '@cetus/ui-kit'
import { cancelBubble } from '@cetus/utils'
import { Box, Button, Flex, FlexProps, HStack, Menu, MenuButton, MenuButtonProps, MenuList, Text, VStack } from '@chakra-ui/react'
import { useState } from 'react'

export type SortDropType = { label: string; value: string }

const SortDropBlock = ({
  sortText,
  minW,
  wrapStyle = {},
  currentSort,
  onSortByChange,
  sortByList,
  xlinkHref = '',
  iconOnClick,
  iconBoxStyle
}: {
  sortText: string
  minW?: string
  wrapStyle?: MenuButtonProps
  currentSort: SortDropType
  onSortByChange: (type: SortDropType) => void
  sortByList: any
  xlinkHref?: string
  iconBoxStyle?: FlexProps
  iconOnClick?: () => void
}) => {
  const [isOpen, setIsOpen] = useState(false)
  return (
    <Menu
      isLazy
      isOpen={isOpen}
      placement="bottom-end"
      onClose={() => {
        setIsOpen(false)
      }}
    >
      <HStack
        minW={minW ? (xlinkHref ? `calc(${minW} + 52px)` : `calc(${minW} + 12px)`) : 'unset'}
        w={minW ? (xlinkHref ? `calc(${minW} + 52px)` : `calc(${minW} + 12px)`) : 'unset'}
        gap="12px"
        bg="bg_secondary"
        borderRadius={{ base: '8px', lg: '12px' }}
        border="1px solid"
        borderColor="border"
        h="40px"
        pl="10px"
      >
        {xlinkHref && (
          <Flex
            w="40px"
            h="40px"
            borderRight="1px solid"
            borderColor="border"
            borderRadius={{ base: '8px', lg: '12px' }}
            justify="center"
            align="center"
            ml="-10px"
            {...iconBoxStyle}
          >
            <Icon
              xlinkHref={xlinkHref}
              variant="gray"
              onClick={e => {
                cancelBubble(e)
                setIsOpen(false)
                iconOnClick?.()
              }}
            />
          </Flex>
        )}
        <MenuButton
          w={minW || 'unset'}
          isActive={isOpen}
          bg="bg_secondary"
          as={Button}
          p="0"
          pr="10px"
          h="38px"
          sx={{
            _hover: {
              bg: 'bg_secondary',
              '.drop-icon': {
                svg: {
                  fill: 'text_caption'
                }
              }
            },
            _active: {
              bg: 'bg_secondary'
            }
          }}
          borderRadius={{ base: '8px', lg: '12px' }}
          {...wrapStyle}
          onClick={() => {
            setIsOpen(true)
          }}
        >
          <HStack align="center" justifyContent="space-between">
            <HStack gap="8px">
              <Text color="text_caption">{sortText}</Text>
            </HStack>
            <HStack gap="8px">
              <Text color="text_caption">{currentSort?.label}</Text>
              <Box className="drop-icon">
                <Icon
                  svgW="16px"
                  xlinkHref="#icon-icon_arrow"
                  variant="gray"
                  transition="transform 0.5s"
                  transform={isOpen ? 'rotate(180deg)' : 'rotate(0deg)'}
                />
              </Box>
            </HStack>
          </HStack>
        </MenuButton>
      </HStack>
      <MenuList p="4px" opacity="1" overflow="hidden" minW={minW ? (xlinkHref ? `calc(${minW} + 52px)` : `calc(${minW} + 12px)`) : 'unset'}>
        <VStack w="100%" gap="4px">
          {sortByList.map((item: any) => {
            return (
              <Text
                w="100%"
                sx={{
                  _hover: {
                    bg: 'menu_item_bg'
                  }
                }}
                bg={currentSort?.value == item?.value ? 'menu_item_bg' : 'none'}
                borderRadius="8px"
                h="45px"
                lineHeight="45px"
                textAlign="center"
                color="text_caption"
                key={item.value}
                cursor="pointer"
                onClick={() => {
                  onSortByChange(item)
                  setIsOpen(false)
                }}
              >
                {item.label}
              </Text>
            )
          })}
        </VStack>
      </MenuList>
    </Menu>
  )
}

export default SortDropBlock
